body,
h1,
h2,
h3,
h4,
h5,
h6,
hr,
p,
blockquote,
dl,
dt,
dd,
ul,
ol,
li,
pre,
select form,
fieldset,
legend,
button,
input,
textarea,
th,
td {
  margin: 0;
  padding: 0;
  outline: none;
}
body {
  background: #F5F5F5!important;
  font-family: "苹方", "Helvetica Neue", Helvetica, Arial, sans-serif;
}
a {
  color: #657180;
  text-decoration: none;
}
/*清除iview 的样式*/
a:hover {
  text-decoration: blink;
}
a:active {
  text-decoration: blink;
}
/*end*/
em,
i {
  font-style: normal;
}
li {
  list-style: none;
}
img {
  vertical-align: middle;
  display: inline-block;
}
/*左右浮动*/
.fl {
  float: left;
}
.fr {
  float: right;
}
.white {
  color: white;
}
.Black {
  color: #333;
}
.Division {
  width: 100%;
  height: 10px;
  background: #F7F7F7;
  border-bottom: 1px solid #E5E5E5;
}
/*清除浮动*/
.clear:after {
  content: "";
  clear: both;
  display: block;
  height: 0px;
  overflow: hidden;
  visibility: hidden;
}
/*隐藏元素*/
.hide {
  display: none;
}
/*显示元素*/
.show {
  display: inline-block;
}
.dispalyfiex {
  display: flex;
  display: -webkit-flex;
}
.flex1 {
  -webkit-flex: 1;
  -ms-flex: 1;
  flex: 1;
}
.flex2 {
  -webkit-flex: 2;
  -ms-flex: 2;
  flex: 2;
}
.flex3 {
  -webkit-flex: 3;
  -ms-flex: 3;
  flex: 3;
}
.flex4 {
  -webkit-flex: 4;
  -ms-flex: 4;
  flex: 4;
}
.flex5 {
  -webkit-flex: 5;
  -ms-flex: 5;
  flex: 5;
}
/*关于定位*/
.relative {
  position: relative;
}
.absolute {
  position: absolute;
}
.word {
  word-break: break-all;
}
.center {
  text-align: center;
}
/*鼠标变手势*/
.pointer {
  cursor: pointer;
}
/*文字强制换行*/
.word {
  word-break: break-all;
}
/*溢出省略*/
.nowrap {
  display: block;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
/*加粗*/
.b {
  font-weight: bold;
}
.bNo {
  font-weight: normal;
}
