.Header {
  position: fixed;
  z-index: 999;
  width: 100%;
}
.UserLoginSubmit {
  padding: 0px 10px;
  margin-top: 20px;
}
.UserLoginSubmit div {
  width: 100%;
  text-align: center;
  line-height: 44px;
  background: #F3C783;
  color: white;
  border: none;
  font-size: 15px;
  border-radius: 45px;
}
/*插件css 修改*/
.page-infinite-loading {
  width: 15%;
  margin: 0px auto;
}
.ButtonSize {
  font-size: 16px!important;
}
.mint-header-title {
  font-size: 15px;
}
.mint-tab-item-label {
  font-size: 15px;
}
.mint-tab-item-label {
  font-size: 13px;
}
.mint-tab-item-label {
  line-height: 45px!important;
}
.mint-navbar .mint-tab-item {
  padding: 0px!important;
}
.mint-navbar {
  border-bottom: 1px solid #E5E5E5;
}
.mint-navbar .mint-tab-item.is-selected {
  border-bottom: 3px solid #F4AC31;
  color: #F4AC31;
  margin: 0px;
}
.mt-progress-progress {
  background: -webkit-linear-gradient(left, #ECBA41, #DF763F);
  background: -moz-linear-gradient(left, #ECBA41, #DF763F);
  background: -o-linear-gradient(left, #ECBA41, #DF763F);
  background: linear-gradient(left, #ECBA41, #DF763F);
  background-color: #ECBA41;
}
.mint-header-button a:hover,
a:active {
  color: #fff!important;
}
.commodityBanner .commodityBottomTextInfo {
  background: white;
  color: #333;
  font-size: 14px;
  padding: 0px 15px;
}
.commodityBanner .commodityBottomTextInfo div.fl h1 {
  font-size: 18px;
  color: #a00a0d;
  line-height: 30px;
}
.commodityBanner .commodityBottomTextInfo div.fl h1 span {
  font-size: 12px;
  color: #999;
  margin-left: 10px;
}
.commodityBanner .commodityBottomTextInfo div.fr p {
  line-height: 30px;
  font-size: 12px;
  margin-right: 10px;
}
.yd-tab-nav {
  position: fixed!important;
  left: 0px;
  top: 45px;
  width: 100%;
  z-index: 999!important;
}
#app .yd-tab-nav-item {
  line-height: 45px;
}
.Integral {
  margin-top: 8px;
  color: #F4AC31;
}
.Integral div:nth-child(1) {
  width: 35px;
  height: 18px;
  background: #F4AC31;
  color: white;
  text-align: center;
  line-height: 18px;
  border-radius: 3px;
  margin-right: 5px;
}
/*弹窗 样式*/
.popupBox {
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0px;
  top: 0px;
  z-index: 999;
}
.popupBox .popupBg {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  position: fixed;
  left: 0px;
  top: 0px;
  z-index: 99;
}
.popupBox .popupCon {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 99999;
}
.popupBox .popupCon img {
  width: 100%;
}
.closePopupSeat {
  position: absolute;
  right: 13%;
  top: 20%;
}
.closePopupSeat i {
  color: white;
}
.MenuButFo {
  height: 65px;
}
/*分享弹窗*/
.sharePopup {
  width: 100%;
  background: white;
  position: absolute;
  left: 0px;
  bottom: 0px;
  text-align: center;
  z-index: 9999;
}
.sharePopup .popupTitle {
  width: 100%;
  line-height: 35px;
  color: #999;
  border-bottom: 1px solid #e5e5e5;
  font-size: 15px;
}
.sharePopup ul li {
  color: #333;
  float: left;
  width: 33.3333%;
  text-align: center;
  margin-top: 20px;
}
.sharePopup ul li p {
  font-size: 12px!important;
  margin-bottom: 5px;
}
.sharePopup ul li p img {
  width: 40%;
}
.sharePopup .closePopup {
  width: 100%;
  margin-top: 20px;
  line-height: 35px;
  background: #ccc;
  color: #333;
  font-size: 14px;
}
.popupText {
  width: 60%;
  position: absolute;
  left: 50%;
  top: 66%;
  color: white;
  text-align: center;
  transform: translate(-50%, 0);
}
.popupText P {
  font-size: 30px;
}
.popupText span {
  font-size: 14px;
}
.popupText .popupBut {
  width: 100%;
  margin-top: 15px;
  line-height: 35px;
  font-size: 16px;
  background: url("../../assets/images/Financial/but_img@1.5x.png") no-repeat center;
  background-size: 100%;
}
.yd-spinner {
  width: 110px;
  height: 33px;
}
.yd-tab-nav {
  color: #F4AC31 !important;
}
#app .yd-tab-nav-item {
  line-height: 45px;
}
#app .yd-tab-nav-item {
  font-size: 14px;
}
.yd-list-donetip {
  padding: .25rem 0px;
  text-align: center;
}



