
.hide[data-v-a916cb64]{ display: none!important;
}
.bg-loding-box[data-v-a916cb64]{
  text-align: center;
  color: #fff;
  font-weight: 600;
  position: absolute;
  width: 100%;
  left: 0;
  top: 45%;
}
.bg-div[data-v-a916cb64]{
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999999888;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,.2);
}
.demo-spin-icon-load[data-v-a916cb64]{
  -webkit-animation: ani-demo-spin 1s linear infinite;
          animation: ani-demo-spin 1s linear infinite;
}
@-webkit-keyframes ani-demo-spin {
from { -webkit-transform: rotate(0deg); transform: rotate(0deg);
}
50%  { -webkit-transform: rotate(180deg); transform: rotate(180deg);
}
to   { -webkit-transform: rotate(360deg); transform: rotate(360deg);
}
}
@keyframes ani-demo-spin {
from { -webkit-transform: rotate(0deg); transform: rotate(0deg);
}
50%  { -webkit-transform: rotate(180deg); transform: rotate(180deg);
}
to   { -webkit-transform: rotate(360deg); transform: rotate(360deg);
}
}
.demo-spin-col[data-v-a916cb64]{
  height: 100px;
  position: relative;
  border: 1px solid #eee;
}



